import base from './base'
import axios from '@/utils/http'

const statistic ={
    changeGoldlist(params) {
        return axios.post(`${base.sq}/statistic/list_goldchange`, JSON.stringify(params))
    },
    list_goldChangeNoGame(params) {
        return axios.post(`${base.sq}/statistic/list_goldChangeNoGame`, JSON.stringify(params))
    },
    list_giveAndReceive(params) {
        return axios.post(`${base.sq}/statistic/list_giveAndReceive`, JSON.stringify(params))
    },
    list_playerAccount(params) {
        return axios.post(`${base.sq}/statistic/list_playerAccount`, JSON.stringify(params))
    },
    getUidList(params) {
        return axios.post(`${base.sq}/backstage/user/get_user_info_bwb`, JSON.stringify(params))
    },
}

export default statistic


