import VueRouter from 'vue-router'
const routes = [
  {
    path: '*', // 404 页面
    component: () => import('../components/404.vue') // 或者使用component也可以的
  },
  {
    path: '/',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('../views/HomePage.vue'),
    meta: [],
    children: [
      //App配置
      {
        path: '/sys/channelConfig',
        name: 'channelConfig',
        component: () => import('../views/app/ChannelConfig.vue'),
        meta: [
          { title: 'App配置', path: '#' },
          { title: '获取频道列表信息', path: '/sys/channelConfig' }
        ]
      },
      {
        path: '/sys/fbSecret',
        name: 'fbSecret',
        component: () => import('../views/app/fbSecretList.vue'),
        meta: [
          { title: 'App配置', path: '#' },
          { title: '获取FaceBook密匙', path: '/sys/fbSecret' }
        ]
      },
      //游戏管理
      {
        path: '/game/sysMsg',
        name: 'SysMessage',
        component: () => import('../views/game/SysMsgSend.vue'),
        meta: [
          { title: '游戏管理', path: '#' },
          { title: '全服系统消息', path: '/game/sysMsg' }
        ]
      },
      {
        path: '/game/nodeList',
        name: 'NodeList',
        component: () => import('../views/game/NodeList.vue'),
        meta: [
          { title: '游戏管理', path: '#' },
          { title: '节点列表', path: '/game/nodeList' }
        ]
      },
      {
        path: '/game/gameList',
        name: 'GameList',
        component: () => import('../views/game/GameList.vue'),
        meta: [
          { title: '游戏管理', path: '#' },
          { title: '游戏列表', path: '/game/gameList' }
        ]
      },
      {
        path: '/game/roomList',
        name: 'RoomList',
        component: () => import('../views/game/RoomList.vue'),
        meta: [
          { title: '游戏管理', path: '#' },
          { title: '房间列表', path: '/game/roomList' }
        ]
      },
      {
        path: '/game/userControl',
        name: 'UserControl',
        component: () => import('../views/game/UserControl.vue'),
        meta: [
          { title: '游戏管理', path: '#' },
          { title: '用户调控', path: '/game/userControl' }
        ]
      },
      //邮件管理
      {
        path: '/mail/sendMail',
        name: 'SendMail',
        component: () => import('../views/mail/SendMail.vue'),
        meta: [{ title: '邮件管理', path: '#' }, { title: '发送邮件', path: '/mail/sendMail' }]
      },
      //活动管理
      {
        path: '/activity/turntableConfig',
        name: 'TurntableConfig',
        component: () => import('../views/activity/TurntableConfig.vue'),
        meta: [
          { title: '活动管理-转盘', path: '#' },
          { title: '获取转盘配置', path: '/activity/turntableConfig' }
        ]
      },
      //数据中心
      {
        path: '/statistic/playerInfo',
        name: 'PlayerInfo',
        component: () => import('../views/statistic/PlayerInfo.vue'),
        meta: [
          { title: '数据中心', path: '#' },
          { title: '玩家信息', path: '/statistic/playerInfo' }
        ]
      },
      {
        path: '/statistic/goldChange',
        name: 'GoldChange',
        component: () => import('../views/statistic/GoldChange.vue'),
        meta: [
          { title: '数据中心', path: '#' },
          { title: '玩家金币变更', path: '/statistic/goldChange' }
        ]
      },
      {
        path: '/statistic/recharge',
        name: 'Recharge',
        component: () => import('../views/statistic/Recharge.vue'),
        meta: [
          { title: '数据中心', path: '#' },
          { title: '玩家充值', path: '/statistic/recharge' }
        ]
      },
      {
        path: '/statistic/give',
        name: 'Give',
        component: () => import('../views/statistic/Give.vue'),
        meta: [
          { title: '数据中心', path: '#' },
          { title: '玩家赠送', path: '/statistic/give' }
        ]
      },
      {
        path: '/statistic/realTimeData',
        name: 'RealTimeData',
        component: () => import('../views/statistic/RealTimeData.vue'),
        meta: [
          { title: '数据中心', path: '#' },
          { title: '玩家实时数据', path: '/statistic/realTimeData' }
        ]
      },
      {
        path: '/statistic/gameRecord',
        name: 'GameRecord',
        component: () => import('../views/statistic/GameRecord.vue'),
        meta: [
          { title: '数据中心', path: '#' },
          { title: '玩家游戏记录', path: '/statistic/gameRecord' }
        ]
      },
      {
        path: '/statistic/dataSummary',
        name: 'DataSummary',
        component: () => import('../views/statistic/DataSummary.vue'),
        meta: [
          { title: '数据中心', path: '#' },
          { title: '数据统计', path: '/statistic/dataSummary' }
        ]
      },

      //后台用户
      {
        path: '/user/list',
        name: 'UserList',
        component: () => import('../views/user/UserList.vue'),
        meta: [
          { title: '后台用户管理', path: '#' },
          { title: '用户列表', path: '/user/list' }
        ]
      },

    ]
  }
]

// 解决ElementUI导航栏中的vue-router在3.0版本以上重复点菜单报错问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const router = new VueRouter({
  // mode: 'history',
  routes: routes
})

export default router



